		],
		daiChuKu:0,
		yiChuKu:0,
		yiChuKuJinE:0,
		qianKeHuNum:0,
	}
  },
  methods:{
	profitClick(index){
		this.$emit("profitClick",index)
	},
	//计算总属性
	MatchProductNum(arr){
		if(arr.length<=0) return 0
		let res=0
		arr.forEach(obj=>{
			res+=parseInt(obj.num)
		})
		return res
	}
  },
  props:{
	profitArrs:{
		type:Array,
		default(){
			return []
		}
	}
  },
  computed:{
	allOrderNum(){
		return 100
	},
	allOrderPrice(){
		return 50
	},
	profit(){
		return this.profitArrs
	}
	
  },
	//对象就不监听也有值 但是数组就不能了
	watch:{
		profit(newVal){
			this.yiChuKu=this.MatchProductNum(newVal)
			this.daiChuKu=newVal.length
		}
	}
}
</script >

